html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  font-family: IBM Plex Sans, sans-serif;
  font-weight: 400;
  position: relative;
  overflow-x: hidden;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

li {
  font-family: Roboto, sans-serif;
}

a {
  color: inherit;
  text-decoration: none;
}

button {
  font: inherit;
  cursor: pointer;
}

.container {
  width: 100%;
  margin: 0 auto;
}

@media screen and (width <= 1200px) {
  .container {
    padding: 0 15px;
  }
}

@media screen and (width >= 479px) and (width <= 767px) {
  .container {
    width: 480px;
  }
}

@media screen and (width >= 768px) and (width <= 1199px) {
  .container {
    max-width: 768px;
    padding: 0 32px;
  }
}

@media screen and (width >= 1199px) {
  .container {
    max-width: 1180px;
  }
}

.hidden {
  opacity: 0;
  width: 1px;
  height: 1px;
  margin-bottom: -1px;
  overflow: hidden;
}

*, :after, :before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

a {
  cursor: pointer;
  text-decoration: none;
}

ul li {
  margin: 0;
  padding: 0;
  list-style: none;
}

button {
  cursor: pointer;
  border: none;
}

img {
  display: block;
}

