  z-index: 1;
  will-change: transform, opacity;
  opacity: .75;
  background: url("cloud.fd3bffcf.png") center no-repeat;
  width: 159px;
  height: 108px;
  animation: 12s ease-in-out -12s infinite cloud-wind;
