    z-index: 1;
    will-change: transform, opacity;
    background: url("cloud.fd3bffcf.png") center no-repeat;
    flex-shrink: 0;
    width: 159px;
    height: 108px;
    animation: 6s ease-in-out -6s infinite cloud-wind;
    position: absolute;
    top: 31px;
    right: 35px;
  }
